.global-header__search {
    @include media('<small') {
        margin: $measure--half;
    }

    @include media('>=small') {
        width: 100%;
    }

    .global-header__search__wrapper {
        @include content-style();

        align-items: center;
        display: inline-flex;
        flex-direction: row;
        flex-wrap: nowrap;
        justify-content: center;
        margin-top: 0;

        @include media('<small') {
            @include betatype();

            flex-direction: column-reverse;
        }
    }

    > a,
    .global-header__search__button {
        @include media('<small') {
            @include cta--border--subelement(
                transparent,
                $color-nearblack,
                $color-gray6,
                $color-nearblack,
                $color-purewhite,
                $color-gray6,
                $color-purewhite,
                square,
                'global-header__search__wrapper'
            );

            display: flex;
            height: $measure--medium7;
            padding-bottom: 0;
            padding-left: 0;
            padding-right: 0;
            padding-top: 0;
            width: $measure--medium7;
        }

        .global-header__search__text {
            @include smalltype();

            font-weight: $font-weight-book;
        }

        @include media('<small') {
            .global-header__search__icon {
                font-size: inherit;
            }

            .global-header__search__text {
                margin-top: $measure--quarter;
            }
        }

        @include media('>=small') {
            @include cta--basic--subelement(
                $color-purewhite,
                $color-red50,
                $color-purewhite,
                $color-red70,
                $color-purewhite,
                square,
                'global-header__search__wrapper'
            );

            display: flex;
            height: 100%;

            .global-header__search__icon {
                margin-left: $measure--half;
            }
        }

        @include media('>=desktop') {
            @include h5style();

            margin-top: 0;
        }

        align-items: center;
        flex-direction: row;
        flex-wrap: nowrap;
        justify-content: center;
        min-height: $minimum-control-height;
        width: 100%;
    }
}

.js .global-header__search {
    .global-header__search__button[aria-expanded='false'] {
        & + .global-header__search__modal {
            display: none;
        }
    }

    .global-header__search__button[aria-expanded='true'] {
        @include media('<small') {
            @include cta--basic--subelement(
                $color-purewhite,
                $color-red50,
                $color-purewhite,
                $color-red70,
                $color-purewhite,
                square,
                'global-header__search__wrapper'
            );

            border: 0;
            margin-right: $measure--half;
            margin-top: $measure--half;
            padding-left: 0;
            padding-right: 0;
        }

        & + .global-header__search__modal {
            @include box-shadow();

            background: linear-gradient($color-red50, $color-red100);
            color: $color-purewhite;
            display: block;
            left: 0;
            min-height: 100vh;
            position: absolute;
            right: 0;
            top: 0;
            z-index: z-index('navigation');

            .global-header__search__modal__header {
                @include box-shadow();

                background-color: $color-red50;

                @include media('<small') {
                    align-items: flex-start;
                    display: flex;
                    flex-direction: row;
                    flex-wrap: nowrap;
                    justify-content: space-between;
                }

                @include media('>=small') {
                    @include default-padding();
                    @include standard-grid();

                    grid-template-rows: repeat(3, 1fr);
                }

                @include media('>=desktop') {
                    grid-row-gap: $measure--basic;
                    grid-template-rows: repeat(2, 1fr);
                }

                .global-header__search__button {
                    grid-column: 22 / span 3;
                }
            }

            .global-header__search__modal__body {
                @include media('<small') {
                    @include default-padding();
                }

                @include media('>=small') {
                    @include standard-box();
                }

                h2 {
                    @include h2style();

                    text-align: center;
                }

                form {
                    margin-top: $content-element-margin;
                }
            }

            .global-header__search__modal__body__results {
                @include media('>=small') {
                    @include standard-grid();

                    .search--inline__suggestions,
                    .search--inline__results__updating--suggestions {
                        grid-column: 1 / span 12;
                    }

                    .search--inline__topresults,
                    .search--inline__results__updating--topresults {
                        grid-column: 13 / span 12;
                    }
                }

                .search--inline__topresults,
                .search--inline__results__updating--topresults {
                    .card {
                        margin-top: $measure--basic;

                        &:not(.card--contact) {
                            a {
                                @include card-anchor-colors(
                                    $background: $color-purewhite,
                                    $color: $color-nearblack,
                                    $background-hover: $color-gray1,
                                    $color-hover: $color-nearblack,
                                    $color-outline: $color-purewhite,
                                    $icon: $color-red50,
                                    $arrow-background: $color-nearblack,
                                    $arrow-color: $color-purewhite,
                                    $arrow-background-hover: $color-red50,
                                    $arrow-color-hover: $color-purewhite,
                                    $border: $color-gray1
                                );

                                box-shadow: none;
                            }
                        }

                        &.card--contact {
                            a {
                                @include anchor-inline(
                                    $color: $color-purewhite,
                                    $visited: $color-purewhite,
                                    $hover: $color-nearblack,
                                    $outline: $color-purewhite
                                );
                            }

                            .card__details .card__tag .icon {
                                color: $color-purewhite;
                            }
                        }

                        .card__details {
                            display: flex;
                            flex-wrap: nowrap;

                            @include media('<small') {
                                flex-direction: column;
                            }

                            @include media('>=small') {
                                flex-direction: row;
                            }
                        }

                        .card__thumbnail + .card__details__text {
                            @include media('<small') {
                                padding-left: 0;
                                padding-right: 0;
                            }

                            @include media('>=small') {
                                padding-top: 0;
                            }
                        }

                        .card__thumbnail {
                            flex-shrink: 0;
                            height: $measure--huge5;
                            width: $measure--huge5;
                        }
                    }
                }
            }

            .header-logo {
                @include media('<small') {
                    align-self: center;
                    justify-content: flex-start;
                    padding-bottom: $measure--half;
                    padding-left: $measure--half;
                    padding-right: $measure--half;
                    padding-top: $measure--half;
                    width: $measure--huge5;
                }

                @include media('>=small') {
                    grid-column-end: span 3;
                    grid-column-start: 1;
                    grid-row-end: span 2;
                    grid-row-start: 1;
                }

                a {
                    @include anchor-img();

                    display: inline-block;
                    width: 100%;

                    @include media('<small') {
                        height: $measure--medium7;
                    }

                    @include media('>=small') {
                        margin-bottom: $measure--small2;
                        margin-top: $measure--small2;
                    }

                    &:active,
                    &:focus {
                        outline-color: $color-purewhite;
                    }

                    .logo {
                        width: 100%;

                        @include media('<small') {
                            height: 100%;
                        }
                    }
                }
            }
        }
    }

    .veil--search {
        @include veil();

        &[data-on='true'] {
            position: fixed;
        }
    }

    .search--inline__query__icon--wrapper {
        background-color: $color-purewhite;
        padding-bottom: $measure--half;
        padding-left: $measure--half;
        padding-right: $measure--basic;
        padding-top: #{$measure--quarter + $border-width-hairline};

        @include media('<tablet') {
            display: none;
        }
    }

    .search--inline__query {
        > div {
            display: flex;
            flex-direction: row;
            flex-wrap: nowrap;

            button {
                @include cta--basic--subelement(
                    $color: $color-nearblack,
                    $background: $color-purewhite,
                    $hover-color: $color-nearblack,
                    $hover-background: $color-gray6,
                    $outline: $color-nearblack,
                    $corners: square,
                    $class: 'search--inline__query__submit__content'
                );

                @include media('<small') {
                    margin-left: $measure--basic;

                    .search--inline__query__submit__text {
                        @include sr-only();
                    }
                }

                @include media('>=small') {
                    margin-left: $measure--small2;

                    .search--inline__query__submit__icon {
                        display: none;
                    }
                }
            }
        }
    }

    .search--inline__icon {
        background-color: $color-purewhite;
        color: $color-gray7;
        font-weight: $font-weight-text;
        margin-bottom: $measure--half;
        margin-top: $measure--threequarters;
    }

    .search--inline__query__input {
        @include font-family('default');
        @include body-font-features();
        @include alphatype();

        @include media('>=tablet') {
            @include gammatype();
        }

        background-color: $color-purewhite;
        display: flex;
        flex-grow: 1;
        font-weight: $font-weight-text;
        padding: 0;
        position: relative;

        &:focus-within {
            @include outline-properties($color-purewhite);
        }

        input {
            @include font-family('default');
            @include body-font-features();
            @include gammatype();

            border: $border-width-hairline solid transparent;
            font-style: normal;
            height: 100%;
            min-width: 0; // Constrains width in Firefox
            padding-bottom: $measure--half;
            padding-left: $measure--half;
            padding-right: $measure--medium5;
            padding-top: $measure--half;
            width: 100%;

            &:focus {
                color: $color-gray10;
                font-style: normal;
                font-weight: $font-weight-medium;
                outline: 0;
            }

            &::placeholder {
                font-style: italic;
            }

            &:placeholder-shown {
                color: $color-gray7;
            }
        }

        .search--inline__clear {
            @include epsilontype();

            align-items: center;
            display: flex;
            flex-direction: row;
            flex-wrap: nowrap;
            height: $measure--small5;
            justify-content: center;
            position: absolute;
            right: $measure--half;
            top: $measure--quarter;
            width: $measure--small5;

            &:focus {
                @include outline-properties();

                outline-offset: -#{$border-width-thicker};
            }

            .search--inline__clear__text {
                @include sr-only();
            }

            .icon {
                --fa-primary-color: #{$color-gray7};
                --fa-primary-opacity: 1;
                --fa-secondary-color: #{$color-gray1};
                --fa-secondary-opacity: 1;

                letter-spacing: 0;
                line-height: 1;
            }
        }
    }

    .search--inline__results__updating {
        @include gammatype();

        margin-bottom: $heading-margin;
        margin-top: $heading-margin;

        .icon {
            color: $color-purewhite;
        }
    }

    .search--inline__suggestions {
        ul {
            margin-left: $measure--small1;
            max-width: calc(#{$max-line-length} - #{$measure--small1});
        }

        li {
            @include betatype();

            margin-top: $content-element-margin;
        }

        a {
            @include anchor-inline(
                $color: $color-purewhite,
                $visited: $color-purewhite,
                $hover: $color-nearblack,
                $outline: $color-purewhite
            );

            font-weight: $font-weight-light;

            .search--inline__suggestion {
                font-weight: $font-weight-bold;
            }
        }
    }
}
